<template>
  <div class="home">
    <div class="place"></div>
    <div class="aoe-area" :style="{transform: 'rotate('+45*randomPos+'deg)'}">
      <div
        v-for="(col, cindex) in cols"
        v-show="col.show"
        :key="cindex"
        :style="{ left: cindex * 40 + 'px' }"
        class="aoe-cols"
      >
        <div
          v-for="(row, rindex) in col.rows"
          v-show="row.show"
          :key="rindex"
          :style="{ top: rindex * 40 + 'px' }"
          class="aoe-circle"
        >
          <span>︾</span>
        </div>
      </div>
      <span class="mark-1" :style="{transform: 'rotate('+(-45*randomPos)+'deg)'}">1</span>
    </div>
    <div class="button" @click="createRandom">生成地火</div>
  </div>
</template>

<script>
export default {
  name: "Home",
  data() {
    return {
      cols: [
        {
          name: "0",
          show: false,
          rows: [
            { name: "0", show: false },
            { name: "1", show: false },
            { name: "2", show: false },
            { name: "3", show: false },
            { name: "4", show: false },
            { name: "5", show: false },
          ],
        },
        {
          name: "1",
          show: false,
          rows: [
            { name: "0", show: false },
            { name: "1", show: false },
            { name: "2", show: false },
            { name: "3", show: false },
            { name: "4", show: false },
            { name: "5", show: false },
          ],
        },
        {
          name: "2",
          show: false,
          rows: [
            { name: "0", show: false },
            { name: "1", show: false },
            { name: "2", show: false },
            { name: "3", show: false },
            { name: "4", show: false },
            { name: "5", show: false },
          ],
        },
        {
          name: "3",
          show: false,
          rows: [
            { name: "0", show: false },
            { name: "1", show: false },
            { name: "2", show: false },
            { name: "3", show: false },
            { name: "4", show: false },
            { name: "5", show: false },
          ],
        },
        {
          name: "4",
          show: false,
          rows: [
            { name: "0", show: false },
            { name: "1", show: false },
            { name: "2", show: false },
            { name: "3", show: false },
            { name: "4", show: false },
            { name: "5", show: false },
          ],
        },
        {
          name: "5",
          show: false,
          rows: [
            { name: "0", show: false },
            { name: "1", show: false },
            { name: "2", show: false },
            { name: "3", show: false },
            { name: "4", show: false },
            { name: "5", show: false },
          ],
        },
      ], //row nums
      aoeArray: [],
      randomPos: 0
    };
  },
  mounted(){
    // this.cols.forEach(v=>{
    //   v.show = true
    //   v.rows.forEach(x=>{
    //     x.show = true
    //   })
    // })
  },
  methods: {
    initAoeGroupArray() {
      let all = [
        [ [0, 1], [2, 3], [4, 5] ],
        [ [0, 1], [2, 4], [3, 5] ],
        [ [0, 2], [1, 3], [4, 5] ],
        [ [0, 2], [3, 4], [5, 1] ],
        [ [1, 2], [3, 4], [5, 0] ],
        [ [1, 2], [3, 5], [4, 0] ],
        [ [1, 3], [2, 4], [5, 0] ],
        [ [2, 3], [4, 0], [5, 1] ],
      ]//所有不同组合
      let one = Math.floor(Math.random() * 8); //生成随机数0-7
      let temp = all[one] //取出一个可能
      //每个可能还有6种顺序
      let res = this.permute(temp)
      let rd = Math.floor(Math.random() * 6); //生成随机数0-5
      this.aoeArray = res[rd]
    },
    permute(nums){
        const res = [], path = [];
        backtracking(nums, nums.length, []);
        return res;
        
        function backtracking(n, k, used) {
            if(path.length === k) {
                res.push(Array.from(path));
                return;
            }
            for (let i = 0; i < k; i++ ) {
                if(used[i]) continue;
                path.push(n[i]);
                used[i] = true; // 同支
                backtracking(n, k, used);
                path.pop();
                used[i] = false;
            }
        }     
    },
    createRandom() {
      this.randomPos = Math.floor(Math.random() * 8)
      this.initAoeGroupArray()
      // let count = 0

      //第一组
      this.cols[this.aoeArray[0][0]].show = true
      this.cols[this.aoeArray[0][1]].show = true

      this.cols[this.aoeArray[0][0]].rows[0].show = true
      this.cols[this.aoeArray[0][1]].rows[0].show = true
      
      setTimeout(()=>{
        let i = 0
        let firstid = setInterval(() => {
          this.cols[this.aoeArray[0][0]].rows[i].show = false
          this.cols[this.aoeArray[0][1]].rows[i].show = false
          if(i+1<6){
            this.cols[this.aoeArray[0][0]].rows[i+1].show = true
            this.cols[this.aoeArray[0][1]].rows[i+1].show = true
          }
          i++
          if(i>5){
            clearInterval(firstid)
            this.cols[this.aoeArray[0][0]].show = false
            this.cols[this.aoeArray[0][1]].show = false
          }
        }, 1000)
      }, 2000)

      //第二组
      setTimeout(() => {
        this.cols[this.aoeArray[1][0]].show = true
        this.cols[this.aoeArray[1][1]].show = true

        this.cols[this.aoeArray[1][0]].rows[0].show = true
        this.cols[this.aoeArray[1][1]].rows[0].show = true
        
        setTimeout(()=>{
          let i = 0
          let firstid = setInterval(() => {
            this.cols[this.aoeArray[1][0]].rows[i].show = false
            this.cols[this.aoeArray[1][1]].rows[i].show = false
            if(i+1<6){
              this.cols[this.aoeArray[1][0]].rows[i+1].show = true
              this.cols[this.aoeArray[1][1]].rows[i+1].show = true
            }
            i++
            if(i>5){
              clearInterval(firstid)
              this.cols[this.aoeArray[1][0]].show = false
              this.cols[this.aoeArray[1][1]].show = false
            }
          }, 1000)
        }, 2000)
      }, 2000)

      //第三组
      setTimeout(() => {
        this.cols[this.aoeArray[2][0]].show = true
        this.cols[this.aoeArray[2][1]].show = true

        this.cols[this.aoeArray[2][0]].rows[0].show = true
        this.cols[this.aoeArray[2][1]].rows[0].show = true
        
        setTimeout(()=>{
          let i = 0
          let firstid = setInterval(() => {
            this.cols[this.aoeArray[2][0]].rows[i].show = false
            this.cols[this.aoeArray[2][1]].rows[i].show = false
            if(i+1<6){
              this.cols[this.aoeArray[2][0]].rows[i+1].show = true
              this.cols[this.aoeArray[2][1]].rows[i+1].show = true
            }
            i++
            if(i>5){
              clearInterval(firstid)
              this.cols[this.aoeArray[2][0]].show = false
              this.cols[this.aoeArray[2][1]].show = false
            }
          }, 1000)
        }, 2000)
      }, 4000)
    }
  }
}
</script>

